<template>
  <div id="vueapp">
    <div>
      <video ref="preview" width="200" height="150" autoplay></video>
    </div>
    <div>
      <button @click="btnRecordClicked" :disabled="recording">Record</button>
      <button @click="btnPauseClicked" :disabled="paused||!recording">Pause</button>
      <button @click="btnResumeClicked" :disabled="!paused||!recording">Resume</button>
      <button @click="btnStopClicked" :disabled="!recording">Stop</button>
      <button :disabled="!currentWebmData" @click="btnPlayClicked">Play</button>
    </div>
    <video controls ref="player" width="400" height="300"></video>
  </div>
</template>

<script>
export default {
  name: "RecDemo",
  data(){
    return{

    }
  },
  mounted() {
    this._initApp()
  },
  methods:{
    async _initApp() {
      // this._stream = await navigator.mediaDevices.getUserMedia({audio: false, video: true});
      this._stream = await navigator.mediaDevices.getDisplayMedia();
      this.$refs.preview.srcObject = this._stream;
      this._recorder = new MediaRecorder(this._stream, {mimeType: "video/webm;codecs=h264"});
      this._recorder.ondataavailable = this.recorder_dataAvailableHandler.bind(this);
    },

    recorder_dataAvailableHandler(e) {
      console.log(e);
      this.currentWebmData = e.data;
    },

    btnRecordClicked() {
      this.recording = true;
      this.paused = false;
      this._recorder.start();
    },
    btnPauseClicked() {
      this.paused = true;
      this._recorder.pause();
    },
    btnResumeClicked() {
      this.paused = false;
      this._recorder.resume();
    },
    btnStopClicked() {
      this.recording = false;
      this._recorder.stop();
    },

    btnPlayClicked() {
      this.$refs.player.src = URL.createObjectURL(this.currentWebmData);
    }
  }

}
</script>

